<template>
    <div class="box">
    <highlightjs :language="route.params.lan" :code="code" />
    </div>
</template>
<script setup>
import { ref } from "vue"
import $ from "jquery";
import { useRoute } from "vue-router";
let Base64 = require('js-base64').Base64
const route = useRoute();
console.log(route, 3)
console.log(4)
let code = ref("请选择题目")
$.ajax({
	type: "GET",
	url: "/"+ route.params.m + "/" + route.params.id + ".txt",
	dataType: "text",
	success: function (res) {
		console.log(res);
		code.value = res;
	}
});
if (route.params.base !== '') {
	let str = ref(route.params.base)
	str.value = str.value.replaceAll('_', '/')
	code.value = Base64.decode(str.value)
}
</script>
<style scoped>
body{
    background: #1e1e1e;
}
.box{
    text-align: left;
}
</style>